import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';

import { Row, Form, Table, Button, Input, Modal, message } from 'antd';

import * as rollAction from '../../redux/rollList';

const { Column } = Table;
const FormItem = Form.Item;

class Roll extends Component {
  state = {
    visible: false,
    result: ''
  };

  hideModel() {
    this.setState({ visible: false });
  }

  handleSubmit(e) {
    e.preventDefault();
    const { name, rank } = this.state;
    if (!name) return message.error('Name must be required');
    if (isNaN(+rank) || +rank <= 0)
      return message.error('Invalid Rank. Rank > 0');

    if (this.props.ROLL_LIST.findIndex(v => v.name === name) >= 0)
      return message.error('Duplicate name');

    this.props.addRollList({
      name: name,
      rank: rank,
      key: name
    });
  }

  onchangeRank(e) {
    const value = e.target.value;
    this.setState({ rank: value });
  }

  onchangeName(e) {
    const value = e.target.value;
    this.setState({ name: value });
  }

  roll() {
    require.ensure(
      [],
      require => {
        const RollUp = require('@axetroy/roll');
        const roller = new RollUp();
        const list = this.props.ROLL_LIST;
        list.forEach(v => roller.add(v.name, +v.rank));
        const result = roller.roll();
        console.info(result);
        this.setState({ result, visible: true });
      },
      'roll'
    );
  }

  render() {
    return (
      <Row>
        <Row>
          <h3>, Roll</h3>
          <p>
            : <Link to="/repo/axetroy/roll">Roll</Link>
          </p>
          <p>, Roll</p>
          <p> = /</p>
        </Row>
        <Row>
          <Modal
            title="ROLL RESULT"
            visible={this.state.visible}
            onOk={() => this.hideModel()}
            onCancel={() => this.hideModel()}
          >
            {this.state.result}
          </Modal>

          <Form layout="inline" onSubmit={e => this.handleSubmit(e)}>
            <FormItem required>
              <Input
                onChange={e => this.onchangeName(e)}
                type="text"
                placeholder=""
              />
            </FormItem>
            <FormItem required>
              <Input
                onChange={e => this.onchangeRank(e)}
                type="text"
                placeholder=""
              />
            </FormItem>
            <FormItem>
              <Button type="primary" htmlType="submit">
                
              </Button>
            </FormItem>
            <FormItem>
              <Button type="primary" onClick={() => this.roll()}>
                ROLL
              </Button>
            </FormItem>
          </Form>
          <Table dataSource={this.props.ROLL_LIST} pagination={false}>
            <Column title="" dataIndex="name" key="name" />
            <Column title="" dataIndex="rank" key="rank" />
            <Column
              title=""
              key="Action"
              render={(data, record) => (
                <span>
                  <Button onClick={() => this.props.removeRollList(data.name)}>
                    Delete
                  </Button>
                </span>
              )}
            />
          </Table>
        </Row>
      </Row>
    );
  }
}
export default connect(
  function mapStateToProps(state) {
    return {
      ROLL_LIST: state.ROLL_LIST
    };
  },
  function mapDispatchToProps(dispatch) {
    return bindActionCreators(
      {
        addRollList: rollAction.add,
        removeRollList: rollAction.remove
      },
      dispatch
    );
  }
)(Roll);



